import React, { Component } from 'react';

class Geolocation extends Component {
	constructor(props) {
	    super(props);
	    this.state = {
	    	list: [],
	    	id: null 
	    };
	}

	initGeo = () => {
		if ('geolocation' in navigator) {
	    	this.setState({id: navigator.geolocation.watchPosition(this.updatePos)});
		}		
	}

	updatePos = (location) => {
		let list = this.state.list;
		let last = list[list.length - 1];
		if (list.length > 0 && (last.latitude !== location.coords.latitude && last.longitude !== location.coords.longitude)) {
			this.setState({
	  			list: [...this.state.list, location.coords]
	  		});
		}

		if (list.length === 0 ) {
			this.setState({
	  			list: [...this.state.list, location.coords]
	  		});
		}
	}

	clearWatch = () => {
		navigator.geolocation.clearWatch(this.state.id);
	}

	  render() {
	    return (
	    <div>
	    	<button onClick={this.initGeo}>Ask for location</button>
	    	<ul>{this.state.list.map((coords, i) => (
	    			<li key={i}>{coords.latitude}</li>
	    		))}</ul>
	    	<button onClick={this.clearWatch}>Stop watch</button>
	    </div>
	    );
	  }
}

export default Geolocation;